El corrent artístic del futurisme, de principis del segle XX i predecessora del cubisme, va ser un estil immortalitzat per artistes de la talla de Bracque o Picasso. La finalitat principal era la de reinventar la història universal, utilitzant colors més exaltats; i, per altra banda, la modernitat com a factor clau d'avantguarda, buscant així noves vies d'expressió.<br><br>

Aquest corrent pictòric va tenir influència en les maneres de concebre la poesia. Així, va donar com a resultat la coneguda actualment com a poesia visual. Ara bé, què és la poesia visual? És una forma experimental amb referències clares de l'Antiga Grècia, substituint posteriorment els caligrames per formes narratives conservadores.<br><br>

Pel que fa a la poesia visual, les imatges, l'art plàstic o les formes pictòriques s'encarreguen de definir un poema i viceversa, convertint-se així en un curiós híbrid molt visual. Un collage realitzat amb versos d'un escrit o imatge amb capacitat per definir la intenció d'un poema per si sola són alguns exemples de poesia visual.</p>


